import {GeneralInfo} from '@/interfaces/general-info'

export default {
  generalInfo(state: any, info: GeneralInfo) {
    const {age, location, transPerWeek, carePrecent,phoneNumber} = info
    state.generalInfo = {age: +age, location, transPerWeek: +transPerWeek, carePrecent: +carePrecent, phoneNumber};
    localStorage.setItem('generalInfo', JSON.stringify(info));
  },
}
