export default {
  generalInfo: (state: any) => {
    return state.generalInfo;
  },
  personalRisk(state: any) {
    const {carePrecent, transPerWeek} = state.generalInfo
    return (100 - carePrecent) / 100 * (transPerWeek);
  },
  locationInfo(state: any) {
    // const {location} = state.generalInfo;
    return {
      avgAge: 30,
      population: 600000,
      infectedsPerDay: 1000,
    }
  },
  allInfected(state: any, getters: any) {
    const {personalRisk, locationInfo: {infectedsPerDay}} = getters;
    return infectedsPerDay * (personalRisk * 2.5) ** 6;
  },
  allDead (state: any, getters: any) {
    const {allInfected} = getters;
    return 6 / 100 * allInfected;
  },
  //catching
  getInfected(state: any, getters: any) {
    const {age, location, transPerWeek, carePrecent, phoneNumber} = state.generalInfo;
    const {allInfected, locationInfo} = getters;
    return allInfected / locationInfo.population;
  },
  causeSomeoneDeath(state: any, getters: any) {
    const {getInfected, personalRisk, locationInfo} = getters;
    return getInfected * ((locationInfo.infectedsPerDay)) * (personalRisk * 2.5) ** 6;
  },
  dying(state: any, getters: any) {
    const {age} = state.generalInfo;
    const {getInfected} = getters;
    let p = 0;
    if (age>80) { p = 1;}
    else if (age>45) {p = (age-45)/35}
    else if (age<10) {p = age/10}
    const pr = .1 + p/1.61
    return pr * getInfected;
  },

}
