<template>
  <div class="statistics">
    <p>if everyone act like you it would be the result in a next month</p>
    <div class="statistic">
      <div class="diagram"></div>
      <p>you catch it = {{catchIt}}</p>
    </div>
    <div class="statistic">
      <div class="diagram"></div>
      <p>you die = {{dying}}</p>
    </div>
    <div class="statistic">
      <div class="diagram"></div>
      <p>all death count = {{allDead}}</p>
    </div>
    <div class="statistic">
      <div class="diagram"></div>
      <p>all infected count = {{allInfected}}</p>
    </div>

    <p>We need you to give us some information everyday, to inform you everyday of its risk</p>
    <div class="actions">
      <router-link to="/daily-info">Next</router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import {mapGetters} from 'vuex';
export default {
  name: 'Home',
  computed: {
    ...mapGetters([
      'allDead',
      'getInfected',
      'allInfected',
      'dying',
      'causeSomeoneDeath',
    ]),
    catchIt() {
      return ((this.$store.getters.getInfected || 0) * 100).toFixed(3)+'%'
    },
    dying() {
      return ((this.$store.getters.dying || 0) * 100).toFixed(3)+'%'
    },
    allInfected() {
      return Math.round(this.$store.getters.allInfected);
    },
    allDead() {
      return Math.round(this.$store.getters.allDead);
    }
  },
  components: {
  }
}
</script>
