<template>
  <div class="daily">
    <div class="questions">
      <div>
        <label>How many has been infected since last day in your city?</label>
        <input v-model="daily.cityInfected" type="text"/>
      </div>
      <div>
        <label>How many guys have you infected today(except your household)?</label>
        <input v-model="daily.infected" type="number"/>
      </div>
      <div v-if="+daily.infected > 0">
        <label>Please enter some of contacts have you infected today?</label>
        <div>
          <ul>
            <li :key="contact" v-for="contact in daily.list">{{contact}}</li>
          </ul>
        </div>
        <input v-model="newContact" type="text"/>
        <button @click="addNewContact">+</button>
      </div>
    </div>
    <div class="actions">
      <router-link to="/daily-statics">Next</router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'DailyInfo',
  methods: {
    addNewContact() {
      this.daily.list.push(this.newContact);
      this.newContact = '';
    }
  },
  data() {
    return {
      newContact: '',
      daily: {
        list: [],
        cityInfected: 'I Dont Know',
        infected: 0,
      }
    }
  },
  components: {
  }
}
</script>
