import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'
Vue.use(Vuex)

const vuex =  new Vuex.Store({
  state,
  mutations,
  getters,
  actions,
  modules: {}
})

const gi = localStorage.getItem('generalInfo');
const initialGi = gi ? JSON.parse(gi) : {age: 0, location: '', transPerWeek: 0, carePrecent: 50, phoneNumber: ''}
vuex.commit('generalInfo', initialGi);

export default vuex
